/**
 * @description Vuex 状态管理主入口
 */

import Vue from 'vue';
import Vuex from 'vuex';
import auth from './modules/auth';

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    auth
  },
  // 严格模式，确保状态变更只能由 mutation 函数引起
  strict: process.env.NODE_ENV !== 'production'
});
